:root {
    --header-height: 106px;
    --footer-height: 132px;
}

main {
    min-height: calc(100vh - var(--header-height) - var(--footer-height));
}

header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1020;
}

footer {
    position: relative;
    margin-top: 20px;
}